.feature-image{
	
	img {
		width: 100%;
		height: auto; 
	}
}


.photo-block {
    background: #fff;
    
    position: relative;
    z-index: 2;

	ul {
	    margin: 0;
	    padding: 0;
	    text-align: center;
	    white-space: nowrap;
	    overflow: hidden;
	    margin-bottom: -15px;
	    margin-top: 2px;
		
		li {
		margin: 0;
		padding: 0;
		display: inline-block;
		}

	}
}

